#menu {
    .language {
        display: flex;
        justify-content: space-evenly;
        margin-bottom: 30px;

        img {
            width: 40px;
            height: 40px;
            border-radius: 80px;
            box-shadow: 0px 0px 2px var(--primary);
            padding: 10px;
        }

        .active {
            box-shadow: 0px 0px 10px var(--primary);
            background-color: lightgray;
        }
    }

    .category-title {
        text-align: center;
        font-size: 2rem;
        text-decoration: underline;
        margin-bottom: 50px;
        margin-top: 75px;
    }

    .food-container {
        display: flex;
        flex-direction: row;
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px dotted black;
    }

    .food-description {
        flex: 1;
        // display: flex;
        // flex-direction: column;

        .food-title {
            font-weight: bold;
            font-size: 1.33rem;
        }

        .food-subtitle {
            color: rgb(66, 66, 66);
            font-size: 1rem;
        }

        .food-allergies {
            padding-top: 10px;
            img {
                padding-right: 4px;
                vertical-align: bottom;
            }
        }
    }

    .food-info {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-between;

        .food-price {
            font-weight: bold;
        }

        .food-special {
            background-color: rgb(250, 231, 215);
            padding: 2px 10px;
            font-style: italic;
            font-size: 0.9rem;
            border-radius: 10px;
        }
    }

    .image {
        width: 100%;
        border-radius: 10px;
    }

    .carne-title {
        margin-top: 20px;
        font-weight: bold;
        font-size: 1.33rem;
    }

    .carne-subtitle {
        margin-top: 5px;
    }

    .tripadvistor {
        margin-top: 40px;
        text-align: center;

        a {
            text-decoration: none;
            color: black;
        }

        img {
            width: 80%;
            border: 1px solid var(--primary);
            border-radius: 20px;
            padding: 10px;
        }

        @media all and (min-width: $breaking-point-phone) {
            img {
                width: 50%;
                
            }
        }
    }
}
