.p-button {
    background-color: var(--primary); /* Green */
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    border-radius: 10px;
}

.p-button-1 {
    background-color: rgb(236, 235, 235); 
    color: var(--primary); 
    border: 2px solid var(--primary);
  }